@import url(https://fonts.googleapis.com/css?family=Anonymous+Pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  
}
/*Colors*/
/* The typing effect */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
/* The typewriter cursor effect */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: black;
  }
}
.typewriter {
  overflow: hidden;
  /* Ensures the content is not revealed until the animation */
  width: 0;
  border-right: 5px solid transparent;
  white-space: nowrap;
  /* Keeps the content on a single line */
  margin: 0 auto;
  /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.25em;
  /* Adjust as needed */
  animation: typing 2.5s steps(40, end) both, blink-caret 0.75s 3;
}
.typewriter-1 {
  animation-delay: 0s;
}
.typewriter-2 {
  animation-delay: 2s;
}
.typewriter-3 {
  animation-delay: 4.5s;
}
body {
  background: url(/resume/static/media/background.3d2c61ce.jpeg) center no-repeat;
  background-size: cover;
  font-family: 'Anonymous Pro', monospace;
}
header {
  position: fixed;
  z-index: 10;
  width: 100%;
  top: 0;
  background: #808080;
}
header a {
  color: white;
}
main {
  margin-top: 20px;
  margin-bottom: 20px;
}
main section {
  min-height: 100vh;
  padding-top: 100px;
}
main .container-fluid {
  padding: 20px;
  border-radius: 10px;
  background: white;
}
main h2 {
  margin-bottom: 30px;
}
main .home {
  background: url(/resume/static/media/home_bg.b76d5025.jpg) bottom no-repeat;
  background-size: cover;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -10;
}
main .home .greetings {
  background-color: white;
  padding: 20px;
  text-align: left;
}
main .home .greetings #nav-buttons-home {
  margin-top: 20px;
}
main .home .greetings h2,
main .home .greetings h4,
main .home .greetings p {
  margin-left: 0;
}
main .home .next-page-btn {
  position: absolute;
  bottom: 30px;
  left: 50vw;
}
main .home .next-page-btn svg {
  color: white;
  transition: all 1s;
}
main .home .next-page-btn svg:hover {
  color: #c0c0c0;
  transform: scale(1.5);
  z-index: 5;
}
main .home .next-page-btn svg:focus {
  border: none;
}
main .home .next-page-btn svg:target {
  border: none;
}
main .about .card {
  border: none;
  margin-bottom: 20px;
}
main .about .about-block {
  position: relative;
  min-height: 70vh;
  margin: 10px;
  padding: 20px;
  border: 1px solid gray;
  border-radius: 10px;
  z-index: 0;
  background: gray;
  transition: all 1s;
}
main .about .about-block:hover {
  background: #c0c0c0;
  transform: scale(1.1);
  z-index: 5;
}
main .about .about-block .btn {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}
main .content {
  margin-top: 100vh;
}
footer {
  background: #808080;
}

